import React, {Component} from 'react';
import {Container, Header, Grid, Image} from 'semantic-ui-react';
import '.././styles.css';
import Divider from "semantic-ui-react/dist/commonjs/elements/Divider";

class Films extends Component{
    render(){
        return (
            <Container className="whoop">
                <Header as='h1'
                        style = {{fontSize:'4em'}}
                        content='Films'
                        textAlign='right'
                />
                <Film title={'China White'} subtitle="Let's gooo." src={require('../Images/lighting.JPG')} size='medium'>
                    China White is a super good movie that we love very much.
                    <br/>
                    <u><i>Awards</i></u>
                    <br/>
                    This Festival, 2017
                    <br/>
                    That Festival, 2018
                </Film>
            </Container>
        );
    }
}

class Film extends Component{
    render(){
        return(
            <Container>
                <Header as='h2' textAlign='center'>
                    {this.props.title}
                    <Header as ='h3' content={this.props.subtitle}/>
                </Header>
                <Grid centered verticalAlign='middle'>
                    <Grid.Row>
                        <Grid.Column width={8}>
                            <p className='newssize'>
                                {this.props.children}
                            </p>
                        </Grid.Column>
                        <Grid.Column width={8} >
                            <Image src={this.props.src} size={this.props.size} centered/>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
                <Divider/>
            </Container>
        );
    }
}

export default Films;