import React, {Component} from 'react';
import {Container,Header} from 'semantic-ui-react';
import '.././styles.css';

class News extends Component{
    render(){
        return (
            <Container className="whoop">
                <Story title='First News Story' date='4/5/2020'>
                    Omg we got some news babyyyy holy mackerel that is seriously
                    the vibe right now and I love it so so so much. How are you
                    doing today? I'm personally doing not so bad.
                </Story>
            </Container>
        );
    }
}

class Story extends Component{
    render(){

        return(
            <Container>
                <Header as='h2'>
                    {this.props.title}
                    <Header as ='h3' content={this.props.date}/>
                </Header>
                <p className='newssize'>
                    {this.props.children}
                </p>
            </Container>
        );
    }
}

export default News;