import React, {Component} from "react";
import {Container, Segment, Grid, Icon} from "semantic-ui-react";

class FootMenu extends Component{
    render(){
        return(
        <Segment inverted basic>
            <Container>
                <Grid divided inverted stackable>
                    <Grid.Row centered>
                        <Grid.Column textAlign='center' width={3}>
                            <Icon name="facebook"/>
                             <a href={"https://facebook.com/zachary.bermack"}>Zack Bermack</a>
                        </Grid.Column>
                        <Grid.Column textAlign='center' width={5}>
                            <Icon name="mail"/>
                            <a href={"mailto:zbermack@conncoll.edu"}>zbermack@conncoll.edu</a>
                        </Grid.Column>
                        <Grid.Column  textAlign='center' width={3}>
                            <Icon name="phone"/>
                            <span className='phonenum'>
                                973.303.7339
                            </span>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
            </Container>
        </Segment>
        );
    }
}

export default FootMenu;